import Head from 'next/head';
import CONFIG from '../../next.config';
const ROOT = (CONFIG.assetPrefix === '') ? '/' : CONFIG.assetPrefix;

export class CommonHead extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <Head>
                <title>{this.props.title}</title>
                <meta name="viewport" content="initial-scale=1.0, width=device-width" />
                <link rel="stylesheet" href="https://fonts.googleapis.com/css?family=Nunito:200,600"></link>
                <link rel="stylesheet" href="https://pro.fontawesome.com/releases/v5.11.2/css/all.css" integrity="sha384-zrnmn8R8KkWl12rAZFt4yKjxplaDaT7/EUkKm7AovijfrQItFWR7O/JJn4DAa/gx" crossorigin="anonymous"></link>
                <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css"></link>
                <link rel="stylesheet" href={ROOT + 'static/css/common.css'}></link>
            </Head>
        );
    }
}

CommonHead.defaultProps = {
    title: 'Yokohama Landscape',
}

export default CommonHead;